<template>
  <div class="info">
    <div class="title"><span></span></div>
    <hr />
    <div class="personal">
      <el-form
        :model="registerForm"
        ref="registerForm"
        label-width="70px"
        class="demo-ruleForm"
        :rule="rules"
      >
        <el-form-item prop="username" label="">
          <el-input v-model="registerForm.username" placeholder=""
            ></el-input
          >
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input
            type="password"
            v-model="registerForm.password"
            placeholder=""
            ></el-input
          >
        </el-form-item>
        <el-form-item prop="sex" label="">
          <el-radio-group v-model="registerForm.sex">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="phoneNumber" label="">
          <el-input
            v-model="registerForm.phoneNumber"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="email" label="">
          <el-input v-model="registerForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="birthday" label="">
          <el-date-picker
            v-model="registerForm.birthday"
            size="normal"
            placeholder=""
            style="width: 100%;"
            :editable="false"
            type="date"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item prop="introduction" label="">
          <el-input
            v-model="registerForm.introduction"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="location" label="">
          <el-select
            v-model="registerForm.location"
            value-key="index"
            placeholder=""
            clearable
            filterable
            style="width: 100%;"
          >
            <el-option
              v-for="city in cities"
              :key="city.value"
              :label="city.label"
              :value="city.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submit"></el-button>
          <el-button type="danger" @click="goback(-1)"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { cities, rules } from "../../assets/data/form";
import { mixin } from "../../mixins/index";
import { getUserById, updateUser } from "../../api/index";

export default {
  name: "user-info",
  mixins: [mixin],
  data() {
    return {
      registerForm: {
        username: "", //
        password: "", //
        sex: "", //
        phoneNumber: "", //
        email: "", //
        birthday: "", //
        introduction: "", //
        location: "" //
      },
      cities: [], //
      rules: [] //
    };
  },
  methods: {
    getMsg(userId) {
      getUserById(userId).then(res => {
        this.registerForm.username = res.username;
        this.registerForm.password = res.password;
        this.registerForm.sex = res.sex;
        this.registerForm.phoneNumber = res.phoneNum;
        this.registerForm.email = res.email;
        this.registerForm.birthday = res.birth;
        this.registerForm.introduction = res.introduction;
        this.registerForm.location = res.location;
      }); //#TODO:
    },
    submit() {
      let d = new Date(this.registerForm.birthday);
      let dateTime =
        d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let params = new URLSearchParams();
      params.append("id", this.userId);
      params.append("username", this.registerForm.username);
      params.append("password", this.registerForm.password);
      params.append("sex", this.registerForm.sex);
      params.append("phoneNum", this.registerForm.phoneNumber);
      params.append("email", this.registerForm.email);
      params.append("birth", dateTime);
      params.append("introduction", this.registerForm.introduction);
      params.append("location", this.registerForm.location);
      params.append("avator", "/avatorImages/defaultUserHeader.jpg");
      updateUser(params)
        .then(res => {
          if (res.code === 1) {
            this.notify("!...", "success");
            setTimeout(() => this.$router.push("login"), 3000);
            this.isLoggedIn = false;
          } else {
            this.notify("", "error");
          }
        })
        .catch(err => this.notify("", err));
    },
    goback(index) {
      this.$router.go(index);
    }
  },
  computed: {
    ...mapGetters([
      "userId", //id
      "isLoggedIn" //
    ])
  },
  created() {
    this.cities = cities;
    this.rules = rules;
    this.getMsg(this.userId);
  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/css/info.scss.";
</style>



