<template>
  <div class="my-music">
    <div class="album-slide">
      <div class="album-img"><img :src="attachImageUrl(avator)" /></div>
      <ul class="album-info">
        <li>{{ userName }}</li>
        <li>{{ userSex }}</li>
        <li>{{ birth }}</li>
        <li>{{ location }}</li>
      </ul>
    </div>
    <div class="album-content">
      <div class="album-title">{{ introduction }}</div>
      <div class="songs-body">
        <album-content :songList="collectLists"
          ><template slot="title"></template></album-content
        >
      </div>
    </div>
  </div>
</template>
<script>
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import { getUserById, collectionOfUserId, songOfSongId } from "../api/index";
import AlbumContent from "../components/search/AlbumContent";
export default {
  name: "my-music",
  mixins: [mixin],
  data() {
    return {
      avator: "", //
      userName: "", //
      userSex: "", //
      birth: "", //
      location: "", //
      introduction: "", //
      collections: [], //
      collectLists: [] //
    };
  },

  components: {
    AlbumContent
  },
  methods: {
    // 
    getMsg() {
      getUserById(this.userId).then(res => {
        this.avator = res.avator;
        this.userName = res.username;
        if (res.sex == 0) {
          this.userSex = "";
        } else if (res.sex == 1) {
          this.userSex = "userS";
        } else {
          this.userSex = "";
        }
        this.birth = this.showBirth(res.birth);
        this.location = res.location;
        this.introduction = res.introduction;
      });
    },
    // 
    getCollection() {
      collectionOfUserId(this.userId)
        .then(res => {
          this.collections = res;
          this.getSongDetail();
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 
    getSongDetail() {
      for (let item of this.collections) {
        songOfSongId(item.songId).then(res => {
          this.collectLists.push(res);
        });
      }
    }
  },
  computed: {
    ...mapGetters([
      "listOfSongs", //
      "userId" //id
    ])
  },
  mounted() {
    this.getMsg();
    this.getCollection();
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/my-music.scss";
</style>



