export const addCollection = params => post(`/collect/add`, params);
// 
export const removeCollection = id => get(`/collect/delete?id=${id}`);
// 
export const allCollections = () => get(`/collect/allCollections`);
// 
export const collectionOfUserId = userId =>
  get(`/collect/collectionOfUserId?userId=${userId}`);



