<template>
  <div>
    <loginLogo></loginLogo>
    <div class="signUp">
      <div class="signUp-head"><span></span></div>
      <el-form
        :model="loginForm"
        ref="loginForm"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item prop="username" label="">
          <el-input v-model="loginForm.username" placeholder=""
            ></el-input
          >
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input
            type="password"
            v-model="loginForm.password"
            placeholder=""
            ></el-input
          >
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submit"></el-button>
          <el-button type="danger" @click="goSignUp"></el-button>
        </div>
      </el-form>
    </div>
  </div></template
>
<script>
import { mixin } from "../mixins/index";
import { login } from "../api/index";
import loginLogo from "../pages/LoginLogo.vue";

export default {
  mixins: [mixin],
  data() {
    return {
      loginForm: {
        username: "", //
        password: "" //
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "" }
        ],
        password: [{ required: true, trigger: "blur", message: "" }]
      }
    };
  },
  methods: {
    submit() {
      let params = new URLSearchParams();
      params.append("username", this.loginForm.username);
      params.append("password", this.loginForm.password);

      login(params)
        .then(res => {
          if (res.code === 1) {
            this.notify("!", "success");
            this.$store.commit("setIsLoggedIn", true);
            this.$store.commit("setUserId", res.userMsg.id);
            this.$store.commit("setUsername", res.userMsg.username);
            this.$store.commit("setAvator", res.userMsg.avator);
            this.changeActiveName("");
            setTimeout(() => this.$router.push("/"), 500);
          } else {
            this.notify("", "error");
          }
        })
        .catch(err => this.notify("", err));
    },
    goSignUp() {
      this.$router.push("/sign-up");
      this.changeActiveName("");
    },
    changeActiveName(activeName) {
      this.$store.commit("setActiveName", activeName);
    }
  },
  mounted() {
    this.changeActiveName("");
  },
  components: {
    loginLogo
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/sign-up.scss.";
</style>



