<template>
  <div class="song-list">
    <ul class="song-list-header">
      <li
        v-for="(item, index) in songStyle"
        :key="index"
        @click="showSongListByStyle(item.name)"
        :class="{ active: item.name == activeName }"
      >
        {{ item.name }}
      </li>
    </ul>
    <div>
      <content-list :contentList="tableData"></content-list>
      <div class="pagination">
        <el-pagination
          @current-change="getCurrentPage"
          :current-page.sync="currentPage"
          :page-sizes="[20, 40, 80, 100]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="albumDatas.length"
          background
        >
          :pager-count="7">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import ContentList from "../components/ContentList.vue";
import { getAllSongList, getSongListLikeStyle } from "../api/index";
import { mixin } from "../mixins";
import { songStyle } from "../assets/data/songLists";
export default {
  name: "song-list",
  mixins: [mixin],
  components: {
    ContentList
  },
  data() {
    return {
      albumDatas: [], //
      pageSize: 15, //
      currentPage: 1, //
      songStyle: [], //
      activeName: "" //
    };
  },
  computed: {
    //
    tableData() {
      return this.albumDatas.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    }
  },
  methods: {
    // 
    getSongList() {
      getAllSongList().then(res => (this.albumDatas = res));
      this.currentPage = 1;
    },
    //
    getCurrentPage(val) {
      this.currentPage = val;
    },
    // 
    getSongListByStyle(style) {
      getSongListLikeStyle(style).then(res => (this.albumDatas = res));
      this.currentPage = 1;
    },
    // 
    showSongListByStyle(name) {
      this.activeName = name;
      this.albumDatas = [];
      if (name == "") {
        this.getSongList();
      } else {
        this.getSongListByStyle(name);
      }
    }
  },
  mounted() {
    this.getSongList();
    this.songStyle = songStyle;
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/css/song-list.scss";
</style>



